.toggle-switch {
    position: relative;
    display: flex;
    width: 6rem;
}
.checkbox {
    display: none;
}
.label {
    display: flex;
    width: 100%;
    cursor: pointer;
    border-radius: 20px;
    justify-content: flex-end;
    box-shadow: 0 3px 15px var(--shadow_param_1), 0 3px 15px var(--shadow_param_2);
    overflow: hidden;
}
.label::before {
    content: '';
    position: absolute;
    inset: 0;
    margin: -1px;
    border-radius: 20px;
    box-shadow: inset 0 -10px 50px var(--backgroundColor);
    z-index: -1;
}
.param_1,
.param_2 {
    width: 50%;
    height: 2.5rem;
    margin-top: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}
.switch {
    width: 2rem;
    height: 2rem;
    margin: 0.5rem;
    background-color: var(--fontColor);
    position: absolute;
    right: 0;
    border-radius: 20px;
    transition: all 0.2s ease-in 0s;
}
.checkbox:checked + .label .switch {
    right: 3rem;
}
